<template>
  <transition name="slide">
    <div class="receipt-area-main">
      <c-nav-bar class="address-nav-bar">
        <i slot="left" class="nav-bar-left iconfont icon-fanhui1"></i>
        <span slot="center" class="nav-bar-center">
          接单地区
        </span>
      </c-nav-bar>
      <div class="receipt-content" ref="receiptContent">
        <div class="first-child">
          <div v-for="(val, index) in get_receiptAreaList" :key="index" class="item">
            <span>
              {{ val.provinceName }} {{ val.cityName }}
            </span>
            <span class="del-text" @click="delCityByCode(val.cityCode)">
              移除
            </span>
          </div>
        </div>
        <div class="add-area-btn" @click="addOneArea">
          <i class="iconfont icon-iconset0186"></i> 添加接单地区
        </div>
      </div>
      <router-view></router-view>
    </div>
  </transition>
</template>
<script>
import BScroll from 'better-scroll'
import { mapGetters,mapActions } from 'vuex'
export default {
  name: 'receiptArea',
  data() {
    return {
      scroll: ''
    }
  },
  computed: {
    ...mapGetters(['get_receiptAreaList'])
  },
  watch: {
    change_receiptAreaList_delOne() {
      this.change_receiptAreaList_delOne()
    }
  },
  mounted() {
    setTimeout(() => {
      this.init()
    }, 20)
  },
  methods: {
    ...mapActions(['change_receiptAreaList_delOne']),
    init() {
      this.scroll = new BScroll(this.$refs.receiptContent, {
        probeType: 1,
        click: true,
        eventPassthrough: 'horizontal'
      })
    },
    delCityByCode(_cityCode) {
      console.log(_cityCode)
      this.change_receiptAreaList_delOne(_cityCode)
    },
    addOneArea() {
      this.$router.push({
        path: '/qualification/receiptArea/address/receiptAreaList'
      })
    },
    refresh() {
      this.scroll && this.scroll.refresh()
    }
  }
}
</script>
<style lang="scss" scoped>
@import '~@/css/mixin.scss';
.receipt-area-main {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: $background-color;
  z-index:1000;
  .address-nav-bar{
    background: #fff;
  }
  .receipt-content{
    position: fixed;
    top: 55px;
    bottom: 0;
    left: 0;
    right: 0;
    .first-child {
      padding-bottom: 84px;
      .item {
        height: 50px;
        display: flex;
        padding: 0 20px;
        align-items: center;
        justify-content: space-between;
        background: #fff;
        color:rgb(26, 26, 26);
        font-size: 16px;
        @include border-bottom-1px();
        &:last-child::after{
          display:none
        }
        .del-text {
          color: $color-theme;
          border: 1px solid $color-theme;
          width:60px;
          height: 26px;
          line-height: 26px;
          text-align: center;
          border-radius: 13px;
        }
      }
    }
    .add-area-btn {
      position: absolute;
      left: 0;
      right: 0;
      bottom: 34px;
      height: 50px;
      line-height: 50px;
      text-align: center;
      font-size: 18px;
      color:#fff;
      background-image: linear-gradient(to right, $color-theme, ($color-gradient-end));
    }
  }
}
</style>
